<template>
  <div id="app">
    <div class="example-area">
    <el-button @click="add"></el-button>
    <el-button @click="edit"></el-button>
    <el-button @click="look"></el-button>
    <form-dialog
      :dialogObj="dialogObj"
      :queryFilter="queryFilter"
      :domFilter="domFilter"
      :handleChange="handleChange"
      @beforeClose="beforeClose"
    >
    </form-dialog>
    <collapse-panel :markdownContent="lsa.default"></collapse-panel>
  </div>
  <api-content :tableDataList="config"></api-content>
  </div>
</template>
<script>
import CollapsePanel from '../components/CollapsePanel'
import ApiContent from '../components/api'
import { config } from '../static/formdialog'
const lsa = require('../components/markdownfiles/FromDialog.md')
export default {
  name: 'formd',
  components: {
    CollapsePanel,
    ApiContent
  },
  data () {
    const initQueryFilter = {
      work_type: '',
      business_status: '',
      datetime: '',
      effect_mode: ''
    }
    const intDomFilter = [
      {
        name: 'work_type',               // sugMapArray [{key: 'key', value: 'value'}]  Object {key: value}
        label: '',
        type: 'select',
        disabled: false,
        sugMap: [
          { label: '', value: 100 },
          { label: '', value: 200 },
          { label: '', value: 101 },
          { label: '', value: 110 },
          { label: '', value: 201 },
          { label: '', value: 202 }
        ]
      },
      {
        name: 'business_status',
        label: '',
        type: 'select',
        disabled: false,
        sugMap: [
          { label: '', value: 1 },
          { label: '', value: 2 }
        ]
      },
      {
        label: '',
        type: 'date',
        name: 'datetime',
        limit: 10,
        disabled: false,
        default: []
      },
      {
        label: '',
        type: 'radio',
        name: 'effect_mode',
        disabled: false,
        sugMap: [
          { label: '', value: 1 },
          { label: '', value: 2 }
        ]
      },
      {
        name: 'aoi',
        label: '',
        type: 'select',
        show: true,
        disabled: false,
        sugMap: [
          { label: '', value: 1 },
          { label: '', value: 2 }
        ]
      },
      {
        name: 'shop',
        label: '',
        type: 'select',
        show: true,
        disabled: false,
        sugMap: [
          { label: '', value: 1 },
          { label: '', value: 2 }
        ]
      }
    ]
    return {
      lsa,
      config,
      dialogObj: {
        title: '',
        visible: false
      },
      queryFilter: JSON.parse(JSON.stringify(initQueryFilter)),      // 
      domFilter: JSON.parse(JSON.stringify(intDomFilter))
    }
  },

  methods: {
    handleChange (name, value) {
      if (name === 'effect_mode' && value === 1) {
        this.domFilter[5].show = false
        this.domFilter[4].show = true
      }
      if (name === 'effect_mode' && value === 2) {
        this.domFilter[4].show = false
        this.domFilter[5].show = true
      }
    },
    beforeClose (attr, type) {
      console.log(attr, type)
      this.dialogObj.visible = false
    },
    add () {
      this.dialogObj.visible = true
    },
    edit () {
      this.queryFilter = {
        work_type: 100,
        business_status: 1,
        datetime: '',
        effect_mode: 1
      }
      this.dialogObj.visible = true
    },
    look () {
      this.queryFilter = {
        work_type: 100,
        business_status: 1,
        datetime: '',
        effect_mode: 1
      }
      this.dialogObj.visible = true
    },
    submit () {
      this.dialogObj.visible = false
    },
    close () {
      this.dialogObj.visible = false
    }
  }

}
</script>
<style lang="less" scoped>
.example-area {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  margin: 15px;
  font-size: 12px;

  .example__info {
    color: #666;
    margin: 10px;
    font-size: 14px;
  }
}
</style>
